.input_wrapper{
    display: block;
    position: relative;
}
.superInput {
    margin:  10px;
    outline: none;
    width: 300px;
    padding: 5px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    transition: all 1s;
}

.superInput:focus {
    transition: all 1s;
    box-shadow: 0 0 10px #646464;
}

.error {
    position:absolute;
    top: 10px;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    left: 320px;
    right: 0px;
    text-align: center;
    color: red;
    animation-name: fadeIn;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes fadeIn {
    25% {
        opacity: 1;
        transform: translateX(-20px);
    }
    50% {
        transform: translateX(30px);
        opacity: 1;
    }
}
