.wrapper_button {
    margin: 5px;
}
.defaultButton,.redButton {
    align-items: center;
    background-color: aqua;
    border: 2px solid #111;
    border-radius: 8px;
    box-sizing: border-box;
    color: #111;
    cursor: pointer;
    display: flex;
    font-size: 16px;
    height: 48px;
    justify-content: center;
    line-height: 24px;
    max-width: 100%;
    padding: 0 25px;
    position: relative;
    text-align: center;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    margin: 20px;
    z-index: 1000  !important;

}

.defaultButton,.redButton {
    font-family: Inter, sans-serif;
}

.defaultButton::after,.redButton::after {
    content: "";
    background-color: #111;
    border-radius: 8px;
    display: block;
    height: 48px;
    left: 0;
    width: 100%;
    position: absolute;
    top: -2px;
    transform: translate(8px, 8px);
    transition: transform .2s ease-out;
    z-index: 0 !important;
}

.defaultButton:hover:after,.redButton:hover:after {

    transform: translate(0, 0);
}

.defaultButton:active,.redButton:active {
    background-color: rgb(1, 235, 235);
    outline: 0;
}

.defaultButton:hover,.redButton:hover {
    outline: 0;
}

@media (min-width: 768px) {
    .defaultButton,.redButton{
        padding: 0 40px;
    }
}
.redButton {
    background-color: #fc2222;
}